:root {
  --link-color: #00acdc;
}

body {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.navLink {
  text-align: center;
  align-items: center;
  height: 60px;
  display: flex;
}

header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.nav {
  text-transform: uppercase;
  height: 100%;
}

.logoLink {
  float: left;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.linkList {
  float: right;
  align-items: center;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

li {
  text-align: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: relative;
}

.linkList > li {
  margin-left: 20px;
}

.linkList > li:first-child {
  margin-left: 0;
}

.is-active, li:hover > a {
  color: var(--link-color);
}

.is-active:after {
  content: "";
  background: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

